import React, { useState, useEffect } from "react";
import { useHistory } from "react-router";
import styled from "styled-components";

import {
  faArrowLeft,
  faSave,
  faTrash,
  faExclamationTriangle,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import World from "../../../../data/world/World";
import { remove, updateWithCallback } from "../../../../services/DatabaseService";

import BackButton from "../../../form_elements/BackButton";
import IconButton from "../../../form_elements/IconButton";
import WorldEditView from "./WorldEditView";
import WorldView from "./WorldView";
import { EditToggle, ToggleLeft, ToggleRight } from "../../../general_elements/ToggleStyle";

interface $Props {
  world: World;
  isNew: boolean;
}

const WorldDetail = ({ world, isNew }: $Props) => {
  const [editMode, setMode] = useState<boolean>(isNew);
  const [worldObj, editWorld] = useState<World>(world);
  const [showAlert, setAlert] = useState<boolean>(false);
  const [message, setMessage] = useState<string>("");
  const [unsavedChanges, setUnsavedChanges] = useState<boolean>(false);
  let history = useHistory();

  const deleteWorld = (worldId: number | undefined) => {
    remove("worlds", worldId);
    history.goBack();
  };

  useEffect(() => {
    if (worldObj !== world) {
      setUnsavedChanges(true);
    }
  }, [worldObj, world]);

  const updateWorld = (tableName: string, worldObj: World) => {
    console.log(worldObj);
    updateWithCallback(tableName, worldObj, (result) => {
      if (result > 0) {
        setUnsavedChanges(false);
        setMessage("Saved successful!");
        setAlert(true);
      } else {
        setMessage("Something went wrong!");
        setAlert(true);
      }
      setTimeout(() => {
        setAlert(false);
      }, 3000);
    });
  };

  const editAndSaveWorld = (world: World) => {
    editWorld(world);
    updateWorld("worlds", world);
  };

  return (
    <>
      <TopBar>
        <BackButton icon={faArrowLeft} action={() => history.goBack()} />
        <EditToggle mode={editMode.toString()}>
          <ToggleLeft onClick={() => setMode(false)}>View</ToggleLeft>
          <ToggleRight onClick={() => setMode(true)}>Edit</ToggleRight>
        </EditToggle>
        {editMode && unsavedChanges && <Icon icon={faExclamationTriangle} />}
        {editMode && (
          <>
            <IconButton onClick={() => updateWorld("worlds", worldObj)} icon={faSave} />
            <IconButton onClick={() => deleteWorld(worldObj.id)} icon={faTrash} />
            {message && showAlert && <Message>{message}</Message>}
          </>
        )}
      </TopBar>
      {editMode ? (
        <WorldEditView world={worldObj} onEdit={(value) => editWorld(value)} />
      ) : (
        <WorldView world={worldObj} onEdit={(value) => editAndSaveWorld(value)} />
      )}
    </>
  );
};

export default WorldDetail;

const TopBar = styled.div`
  color: ${({ theme }) => theme.tile.color};
  background-color: ${({ theme }) => theme.main.backgroundColor};
  font-size: 16px;
  overflow: hidden;
  min-width: calc(100% - 20px);
  height: 45px;
  padding: 10px;
  position: relative;
  z-index: 100;
`;

const Message = styled.div`
  padding: 5px;
  width: 150px;
  height: 30px;
  line-height: 30px;
  border-radius: 5px;
  float: right;
`;

const Icon = styled(FontAwesomeIcon)`
  float: right;
  line-height: 30px;
  display: block;
  height: 30px;
  padding: 10px;
  color: ${({ theme }) => theme.main.highlight};
`;
